import React, { useEffect, useRef, useState } from 'react';
import {
  Table,
  Container,
  Col,
  Row,
  Card,
  CardBody,
  CardImg,
  CardTitle,
  CardText,
  CardHeader,
  CardFooter,
  Alert,
  CardImgOverlay,
} from 'reactstrap';
import ReactPlayer from 'react-player';
import 'react-responsive-carousel/lib/styles/carousel.min.css'; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { useDispatch, useSelector } from 'react-redux';
import { fetchDataWO } from '@app-modules/dashboard/actions';
import './style.scss';
import _ from 'lodash';
import moment from 'moment';
import SimpleBar from 'simplebar-react';
import { convertToCapitalCase } from '@app-utils/Commons';
import { FETCH_LIST_ANTRIAN_WO_SUCCESS } from '@app-modules/dashboard/actionTypes';

function usePrevious(value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

const Dashboard = (props) => {
  const dispatch = useDispatch();
  const player = useRef(null);
  const [list, setList] = useState([]);
  const [bannerPromo, setBannerPromo] = useState([]);
  const { wo, auth } = useSelector((state) => state);
  const action = wo.action;
  const {
    sessionUser: {
      data: { user_branch_id },
    },
  } = auth;
  const prevAction = usePrevious(wo);
  const limit = 6;

  const FetchData = () => {
    dispatch(
      fetchDataWO({
        startDate: moment().format('yyyy/MM/DD'),
        FromDate: moment().format('yyyy/MM/DD'),
      })
    );
  };

  useEffect(() => {
    const intervalId = setInterval(() => {
      FetchData();
      fetch('./promo.json')
        .then(function (res) {
          return res.json();
        })
        .then(function (data) {
          // store Data in State Data Variable
          setBannerPromo(data);
          console.log('data', data);
        })
        .catch(function (err) {
          console.log(err, ' error');
        });
    }, 2000 * 60); // in milliseconds
    return () => clearInterval(intervalId);
  }, [dispatch]);

  useEffect(() => {
    fetch('./promo.json')
      .then(function (res) {
        return res.json();
      })
      .then(function (data) {
        // store Data in State Data Variable
        setBannerPromo(data);
        console.log('data', data);
      })
      .catch(function (err) {
        console.log(err, ' error');
      });
    FetchData();
  }, [dispatch]);

  useEffect(() => {
    if (prevAction && prevAction !== action) {
      onActionChange(action);
    }
  }, [action]);

  function onActionChange(action) {
    const actions = {
      [FETCH_LIST_ANTRIAN_WO_SUCCESS]: () => {
        setList(wo?.response?.listObj);
      },
      DEFAULT: () => {},
    };
    return (actions[action] || actions.DEFAULT)();
  }

  function showPlatNo(val) {
    if (val.length > 2) {
      let plat = val.substr(0, 2);
      const check = /^[a-zA-Z]*$/.test(plat);
      if (check) {
        return val.substr(0, 2) + '-' + val.substr(2, 4) + '-' + val.substr(6);
      } else {
        return val[0] + '-' + val.substr(1, 4) + '-' + val.substr(5);
      }
    }
    return val;
  }
  const itemList = (index, data) => {
    return (
      <div key={index}>
        <Table className="table table-striped mb-0">
          <thead className="table-light">
            <tr>
              <th>NO</th>
              <th>NAMA</th>
              <th>NOPOL</th>
              <th>STATUS</th>
            </tr>
          </thead>
          <tbody>
            {data.map((data, _) => {
              const i =
                index === 0 ? index * limit + _ + 1 : index * limit + _ + 1;
              let status = 'ANTRIAN';
              let color = 'bg-green';
              switch (data.status) {
                case 'OPEN':
                  status = 'ANTRIAN';
                  color = 'bg-primary text-dark';
                  break;
                case 'PROSES':
                  status = 'SEDANG SERVIS';
                  color = 'bg-warning text-dark';
                  break;
                case 'CLOSE':
                  status = 'SELESAI';
                  color = 'bg-success text-dark';
                  break;
                case 'FINISH':
                  status = 'SELESAI';
                  color = 'bg-success text-dark';
                  break;
                default:
                  status = data.status;
                  color = 'bg-success text-dark';
                  break;
              }
              return (
                <tr className="table-light">
                  <th scope="row">{i}</th>
                  <td>{convertToCapitalCase(data.wO_Cont_Name, false)}</td>
                  <td>{showPlatNo(data.wo_Registration_number)}</td>
                  <td className={color}>{status}</td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    );
  };

  const renderList = () => {
    if (_.size(list) > 1) {
      const dataTemp = _.chunk(list, limit);
      return dataTemp.map((data, index) => {
        return itemList(index, data);
      });
    }
    return <div>Belum ada data</div>;
  };

  const filterBanner = _(bannerPromo)
    .filter((z) => z.branchId === user_branch_id)
    .head();

  const listFooter = () => {
    return filterBanner?.banner.map((data, i) => {
      return (
        <div className="rounded" key={i}>
          <img src={data.url} />
        </div>
      );
    });
  };
  const renderFooter = () => {
    return (
      <Card>
        <CardBody>
          <div className="ratio ratio-4x3 rounded">
            <ReactPlayer
              ref={player}
              playing
              loop
              url={filterBanner?.youtube}
              width="100%"
              height="100%"
              className="ratio ratio-4x3 rounded"
            />
          </div>
          <div className="banner rounded">
            <SimpleBar style={{ maxHeight: '500px' }}>
              <Carousel
                showArrows={true}
                showThumbs={false}
                dynamicHeight={false}
                autoPlay
                interval={10000}
                infiniteLoop
                axis="vertical">
                {listFooter()}
              </Carousel>
            </SimpleBar>
          </div>
        </CardBody>
      </Card>
    );
  };
  const renderHeader = () => {
    return (
      <Card>
        <CardBody>
          <nav>
            <Col lg={12}>
              <Card>
                <CardImg
                  className="img-fluid"
                  src={filterBanner?.topBanner}
                  alt="bintang"
                />
              </Card>
            </Col>
          </nav>
        </CardBody>
      </Card>
    );
  };
  return (
    <React.Fragment>
      <Row>
        <Col xl="8">
          <Row>
            {renderHeader()}
            <Col xl="12">
              <Carousel
                showArrows={false}
                showThumbs={false}
                dynamicHeight={false}
                autoPlay
                interval={10000}
                infiniteLoop
                // axis="vertical"
              >
                {renderList()}
              </Carousel>
            </Col>
          </Row>
        </Col>
        {/* <!--footer --> */}
        <Col xl="4">{renderFooter()}</Col>
      </Row>
    </React.Fragment>
  );
};
export default Dashboard;
